<template>
  <div class="page-container">
    <base-breadcrumbs
      :parents="[['Home','/'], ['Klanten', '/klanten'], [currentClient.data().clientName, `/klant/${currentClient.id}`], [currentProject.data().projectName, `/project/${currentProject.id}`]]"
      :currentLocation="currentRoom.data().roomName"
    />
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>
          <base-icon
            iconRef="cube"
            position="left"
          />
          {{currentRoom.data().roomName}}
        </h2>
        <div class="flex-divider"></div>
        <base-button
          type="secondary"
          @click="openEditModal"
        >
          <base-icon
            iconRef="edit"
            position="left"
          />Bewerk
        </base-button>
      </div>
      <div class="page-box-content values">
        <div
          class="value-group"
          v-show="currentClient.data().description != ''"
        >
          <div class="value">{{currentRoom.data().description}}</div>
        </div>
        <div class="value-row">
          <div class="value-group">
            <div class="label">Ruimtesoort</div>
            <div class="value">{{$advicevalues.advieswaarden[currentRoom.data().type].label}}</div>
          </div>
          <div class="value-group">
            <div class="label">Oppervlakte</div>
            <div class="value">
              {{(currentRoom.data().width * currentRoom.data().length) | $noDecimals}} m
              <sup>2</sup>
            </div>
          </div>
          <div class="value-group">
            <div class="label">Inhoud</div>
            <div class="value">
              {{(currentRoom.data().width * currentRoom.data().length * currentRoom.data().height) | $noDecimals}} m
              <sup>3</sup>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="page-box">
      <div class="page-box-title">
        <h3>
          <base-icon
            iconRef="activity"
            position="left"
          />Metingen
        </h3>
        <div class="flex-divider"></div>
        <base-button @click="openCreateModal">
          <base-icon
            iconRef="plus"
            position="left"
          />Nieuwe Meting
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <measurements-data-list
          :dataList="currentRoomMeasurements"
          @click="dataEntryClick($event)"
        />
      </div>
    </div>
    <div class="page-box">
      <div class="page-box-title">
        <h3>
          <base-icon
            iconRef="checkmark-square"
            position="left"
          />Analyse &amp; advies
        </h3>
        <div class="flex-divider"></div>
        <base-button @click="openAdviceModal">
          <base-icon
            iconRef="checkmark-square"
            position="left"
          />Bekijk Analyse &amp; advies
        </base-button>
      </div>
    </div>
  </div>
</template>

<script>
import measurementsDataList from "./data-list-components/measurements-data-list.vue";

export default {
  name: "Room",
  components: {
    measurementsDataList
  },
  computed: {
    currentRoom: function() {
      return this.$store.getters.getRoomsList.find(room => {
        return room.id == this.$route.params.roomId;
      });
    },
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.currentRoom.data().belongsTo;
      });
    },
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    },
    currentRoomMeasurements: function() {
      return this.$store.getters.getMeasurementsList.filter(measurement => {
        return (
          measurement.data().belongsTo == this.currentRoom.id &&
          measurement.data().trashed == false
        );
      });
    }
  },
  methods: {
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "room",
        modalType: "edit",
        shown: true
      });
    },
    openCreateModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "measurement",
        modalType: "create",
        shown: true
      });
    },
    openAnalysisModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "analysis",
        modalType: "view",
        shown: true
      });
    },
    openAdviceModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "advice",
        modalType: "view",
        shown: true
      });
    },
    dataEntryClick: function(value) {
      this.openMeasurement(value);
    },
    openMeasurement: function(targetMeasurementId) {
      this.$router.push(`/meting/${targetMeasurementId}`);
    }
  },
  created: function() {
    this.$setPageTitle(`Ruimte - ${this.currentRoom.data().roomName}`);
  }
};
</script>

<style>
</style>



